.recommended {

  &__products {
    @include pageGrid;
    row-gap: 40px;
  }

  &__product {

    grid-column: span 4;

    @include onDesktop {
      grid-column: span 4;
    }

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onMobile {
      grid-column: 1 / -1;
    }
  }
}
